.fileManagerTitle{
 color: gold;
 text-align: center;
 font-size: 36px;
 margin-bottom: 100px;
 position: relative;
 font-weight: bold;
 .imageSize{
     position: absolute;
     right: 30px;
     top: 50px;
 }
}
.logs{
  font-size: 25px;
  font-weight: bold;
  &:hover{
    color: white;
  }
}
.fileManagerWrapper{
    display: flex;
    gap: 10px;
    flex-direction: row;
    flex-wrap:  wrap ;
    @media (max-width: 600px) {
        flex-direction: column;
    }
    .imageWrapper{
        position: relative;
        @media (max-width: 600px) {
           left: 30px;
        }
        .imageIndex{
            overflow: hidden;
            width: 200px;
            height: 200px;  
            @media (max-width: 600px) {
                width: 90%;
                height: 300px;
            }
               .image{
        width: 200px;
        height: 200px;
        @media (max-width: 600px) {
            width: 90%;
            height: 300px;
        }
        object-fit: cover;
        &:hover{
            transform: scale(1.1);
            transition: 0.5s;
            opacity: 0.7;
    }
  }
}
}
}

.imageWrapper:hover  .ImageName{
    display: block;
    position: absolute;
    color:white;
    text-align: center;
    bottom: 100px;
    transition: 1s;
    left: 30px;
}
.loadAll{
    background-color: rgb(34, 182, 180);
    width: 100%;
    border-radius: 10px 10px 10px 10px ;
    &:hover{
        background-color: rgb(114, 240, 238);
    }
}
.noloadAll{
    display: none;
}
.addItem{
    margin-left: 1%;
    margin-top: 2%;
    background-color: rgb(0, 140, 255);
    color: white;
    width: 250px;
    height: 50px;
    border-radius: 10px 10px 10px 10px ;
    margin-bottom: 40px;
    &:hover{
      background-color: gold;
    }
  }
  .addFileForm{
    margin-top: 1%;
    display: flex;
    flex-direction: column;
    max-width: 20%;
    max-height: 1000px;
    gap: 10px;
    background-color: rgba(0, 0, 0, 0.404);
    margin-bottom: 50px;
    @media (max-width: 1000px) {
      max-width: 100%;
  }
  input{
    height: 30px;
    background-color: rgb(138, 113, 57);
    color: gold;
    border-radius: 10px 10px 10px 10px ;
    width: 80%;
    &:hover{
        background-color: rgba(110, 123, 111, 0.701);
    }
  }
    .noImage{
      background-color: red;
      color: gold;
      border-radius: 10px 10px 10px 10px ;
      width: 80%;
      height: 40px;
     }
     .yesImage{
      background-color: rgb(66, 156, 1);
      border-radius: 10px 10px 10px 10px ;
      color: gold;
      width: 80%;
      height: 40px;
     }

  }
  .imageDescription{
    color : gold
  }
  .customfileinput::-webkit-file-upload-button {
    visibility: hidden;
    width: 1px;

  }
  .customfileinput::before {
    content: 'Uploaded Picture :';
    display: inline-block;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer; 
    font-weight: 700;
     color:black;
  
  }
  .customfileinput:hover::before {
    border-color: black;
  }
  .customfileinput:active::before {
    background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
  }
  
  .customfileinputempty::-webkit-file-upload-button {
    visibility: hidden;
  }
  .customfileinputempty::before{
    content: 'Press to Upload Your Picture Here';
    display: inline-block;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    text-shadow: 1px 1px rgb(2, 0, 0);
    font-weight: 700;
   color:gold;
   width: 1px;
   margin-right: 100%;
  }
  @keyframes triggerAlert {
    0% {opacity:0;}
    50% {opacity: 1;}
    100% {opacity: 0;}
  }
  .alertMessage{
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 99;
    animation-name: triggerAlert;
    animation-duration: 4s;
   }
   .noAlertMessage{
    display: none;
   }
   