.imgModalFile{
    width: 350px;
    height: 250px;
    object-fit: cover;
}
.closeButton{
    width: 100px;
    height: 30px;
    background-color: rgb(73, 133, 254);
    position: relative;
    color: white;
    left: 230px;
    margin-top: 20px;
    &:hover{
        background-color: rgb(109, 156, 251);
    }
}
.deleteButton{
    width: 100px;
    height: 30px;   
    margin-top: 10px;
    background-color: rgb(252, 44, 44);
    &:hover{
        background-color: rgb(67, 67, 67);
        color: white;
    }
}
.downloadButton{
    background-color: rgb(114, 114, 114);
    width: 100px;
    height: 30px;
    margin-top: 10px;
    &:hover{
        background-color: rgb(167, 167, 167);
    }
}